/* PAGE */
.fontBold{
  font-weight: 700;
}
.subHeader::before{
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  height: 13vh;
  background-color: #5D1BB8;
  z-index: -1;
}
.subHeader{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.containerInfoSub{
  display: block;
}
.boardRoomLive{
  background: #d82579;
  background: -moz-linear-gradient(45deg,  #d82579 0%, #fea651 100%);
  background: -webkit-linear-gradient(45deg,  #d82579 0%,#fea651 100%);
  background: linear-gradient(45deg,  #d82579 0%,#fea651 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d82579', endColorstr='#fea651',GradientType=1 );
  padding: 1rem 0;
  font-size: 2.5em;
  border: 1px solid #FCA453;
  border-radius: 2rem;
  margin-bottom: .5rem;
  width: 100%;
  text-align: center;
}
.boardRoomImg{
  margin-top: .5rem;
  vertical-align: top;
  z-index: 1;
  margin-bottom: .5rem;
  max-width: 200px;
  height: auto;
  margin-left: 0.5rem;
  z-index: 0;
}
.pageInfo{
  display: flex;
  flex-direction: column;
  padding: .5rem;
}
h1.titlePage{
    font-size: 3em;
}
.pageInfo span{
  font-size: 1.2em;
  color: #9D94AB;
}
.gridFlex{
  display: flex;
  flex-direction: column;  
}
.baseCard{
  background-color: #4F179B;
  border-radius: 2rem;
  padding: 1rem;
  border: 1px solid #6625BF;
  box-shadow: 0 0 0 0 white;
  transition: box-shadow .3s;
}
.baseCard:hover{
  box-shadow: 0 0 10px 0 #6625BF;
}
@media screen and (min-width: 768px){
  .subHeader{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }
  .boardRoomLive{
    align-self: center;
    margin-top: 0;
    width: auto;
  }
  .boardRoomImg{
    max-width: 250px;
    height: auto;
    margin-left: unset;
  }
  .containerInfoSub{
    display: flex;
    flex-direction: row;
  }
  .pageInfo{
    padding-top: 3rem;
  }
  .boardRoomLive{
    align-self: flex-end;
    padding: 2rem 4rem;
    margin-top: -80px;
  }
}
/* END PAGE */
.harvestCard .label, .stakeCard .label{
  position: absolute;
  left: 0;
  top: 0;
  background-color: #FFD148;
  padding: 1rem 1rem .5rem 1.5rem;
  color: black;
  font-size: 1.2em;
  text-transform: uppercase;
}
.label.pinkLabel{
  color: white;
  background-color: #BB24EC;
}
.tabInfoBoardroom{
  position: relative;
  height: 100%;
  font-size: 1.25em;
  line-height: 1.2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tabsBoardroom .gridFlex:first-child .tabInfoBoardroom div:nth-child(2),
.tabsBoardroom .gridFlex:nth-child(2) .tabInfoBoardroom span{
  color: #00FF6C;
}
.titleTabs{
  color: #9D94AB;
}
.tabsBoardroom .gridFlex:first-child .baseCard{
  background: #c9237d;
  background: -moz-linear-gradient(left,  #c9237d 0%, #701a92 100%);
  background: -webkit-linear-gradient(left,  #c9237d 0%,#701a92 100%);
  background: linear-gradient(to right,  #c9237d 0%,#701a92 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c9237d', endColorstr='#701a92',GradientType=1 );
}
.tabsBoardroom .gridFlex:nth-child(2) .baseCard{
  background: #096F66;
  background: -moz-linear-gradient(left,  #096F66 0%, #3D2E8C 100%);
  background: -webkit-linear-gradient(left,  #096F66 0%,#3D2E8C 100%);
  background: linear-gradient(to right,  #096F66 0%,#3D2E8C 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#096F66', endColorstr='#3D2E8C',GradientType=1 );
}
.tabsBoardroom .gridFlex:first-child .baseCard::before{
  content: "";
  font-size: 1.5em;
  color: white;
  position: absolute;
  right: 0;
  margin-right:1rem;
  width: 1em;
  height: 1em;
  background-image: url('../../assets/img/clock.png');
  background-repeat: no-repeat;
  background-position: center;
  z-index: 0;
}
.tabsBoardroom .gridFlex:nth-child(2) .baseCard::after{
  content: "%";
  font-size: 1.5em;
  color: white;
  position: absolute;
  right: 0;
  margin-right:1rem;
  z-index: 0;
}
.countDownBoard{
  position: relative;
  display: inline-block;
  font-weight: bold;
}
.nextEpoch, .currentEpoch{
  display: inline;
  margin-right: .3rem;
}
.harvestCard, .stakeCard{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.harvestCard{
    background-image: url('../../assets/img/chick.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: soft-light;
}
.mainContainer{
  margin-top: .5rem;
  display: grid;
  height: 100%;
}
.containerInfoCard{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.harvestValue{
  margin-top: .3rem;
}
.harvestValueEarned{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5em;
}

.harvestValueEarned div{
  color: white;
}
.harvestValueEarned div:nth-child(2){
  color: #9D94AB;
  margin-bottom: 1.5rem;
  font-size: .65em;
}
button.claimHarvestOn, button.claimHarvestOff{
  line-height: 2em;
  font-size: 1em;
  color: white;
  margin-bottom: 1rem;
  border-radius: .8rem;
}
button.claimHarvestOn span, button.claimHarvestOff span{
  min-width: 200px;
  max-width: 100%;
}
button.claimHarvestOn, button.claimHarvestOn:hover{
  background-color: #40AC6E!important;
  color: white!important;
  transition: opacity .3s;
}
button.claimHarvestOn:hover{
  opacity: .8;
}
button.claimHarvestOff{
  background-color: red!important;
  color: white!important;
}
.unlockReward{
  color: #FFD148;
  display: inline-block;
  margin-right: .3rem;
}
.unlockRewardCounter{
  display: inline-block;
  color: #FFD148;
}
.harvestBottom{
  margin-top: 1rem;
  background-color: #6525bf62;
}
.harvestBottom{
  text-align: center;
  font-size: .85em;
  color: #9D94AB;
}
.addRemoveButtons{
  display: flex;
  margin-bottom: 1rem;
  justify-content: space-around;
}
.removeFunds button, .addFunds button{
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: .8rem;
  border: 0;
  line-height: 2em;
  height: auto;
  padding: .5rem;
  padding-top: .4rem;
  padding-bottom: 0;
  font-weight: 400;
  font-size: inherit;
  font-size: .55em;
  text-transform: none;
  min-width: 70px;
}
.removeFunds button{
  color: white;
  background-color: red;
}
.addFunds button{
  color: white;
  background-color: #40AC6E;
}
.removeFunds button:disabled{
  color: white;
  background-color: red;
  opacity: .5;
}
.removeFunds button::before{
  content: "-";
  font-size: 5em;
  position: relative;
  display: block;
}
.addFunds button::before{
  content: "+";
  font-size: 5em;
  position: relative;
  display: block;
}
.removeFunds button:hover{
  color: white;
  opacity: .5;
  background-color: red;
}
.addFunds button:hover{
  color: white;
  opacity: .5;
  background-color: #40AC6E;
}
.stakeWithdraw{
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #FFD148;
}
.stakeWithdraw p{
  margin-bottom: 0;
  margin-right: .3rem;
}